<template>
  <v-app class="surface-base apply">
    <v-main class="py-0">
      <v-container>
        <v-row>
          <v-col>
            <v-card max-width="500" class="mx-auto text-center">
              <v-card-title class="font-weight-light grey--text">
                Create Account
              </v-card-title>
              <v-card-text class="pb-0">
                <v-divider></v-divider>
              </v-card-text>
              <v-card-text>
                <!-- START:__FORM::INPUTS__ -->
                <v-form
                  @submit.prevent="login"
                  action="/"
                  ref="form"
                  id="nativeForm"
                >
                  <v-card-text class="pb-0">
                    <v-text-field
                      v-model="email"
                      :rules="[rules.required, rules.email]"
                      outlined
                      label="Email"
                    ></v-text-field>
                    <v-text-field
                      v-model="password"
                      :rules="[rules.required]"
                      outlined
                      :type="showPass ? 'text' : 'password'"
                      :append-icon="showPass ? 'mdi-eye' : 'mdi-eye-off'"
                      @click:append="showPass = !showPass"
                      label="Password"
                    ></v-text-field>
                  </v-card-text>
                  <!-- START:__FORM::SUBMIT__ -->
                  <v-card-actions class="px-4">
                    <v-btn
                      x-large
                      block
                      type="submit"
                      :loading="loading"
                      class="py-5 white--text text-capitalize"
                      color="primary"
                    >
                      <strong>Log In</strong>
                    </v-btn>
                  </v-card-actions>
                  <div link class="mt-3 text--primary font-weight-light">
                    <v-card-text>
                      <router-link to="/login"> Forgotten Password</router-link>
                    </v-card-text>
                    <v-card-actions class="px-4">
                      <v-btn
                        x-large
                        block
                        class="py-5 white--text text-capitalize"
                        rouer
                        to="/signup"
                        color="#16bf82"
                      >
                        <strong>Register</strong>
                      </v-btn>
                    </v-card-actions>
                  </div>
                </v-form>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import { mapState } from "vuex";
export default {
  data: () => ({
    loading: false,
    showPass: false,
    email: "",
    password: "",
    rules: {
      required: (v) => !!v || "required",
      nameLength: (v) => /\b\w{3,10}\b/.test(v) || "more than 3 & less than 10",
      email: (v) =>
        /\b\w{2,20}\b@\b\w{1,15}\b\.\b\w{1,10}\b/.test(v) || "invalid email",
    },
  }),
  methods: {
    login() {
      let nativeForm = document.getElementById("nativeForm");
      if (this.$refs.form.validate()) {
        /*START::CHECK IF USER IN STORAGE */
        if (localStorage.getItem("login-user-info")) {
          let loggedUser = JSON.parse(localStorage.getItem("login-user-info"));
          let loggedFirstName = loggedUser.firstName;
          let loggedLastName = loggedUser.lastName;
          let loggedEmail = loggedUser.email;
          let loggedPassword = loggedUser.password;
          if (loggedEmail == this.email && loggedPassword == this.password) {
            this.loading = true;
            localStorage.setItem(
              "snack",
              JSON.stringify({
                active: true,
                text: "Welcome Back",
                color: "indigo",
              })
            );
            localStorage.setItem(
              "user-info",
              JSON.stringify({
                firstName: loggedFirstName,
                lastName: loggedLastName,
                email: this.email,
                password: this.password,
                id: (
                  Math.random().toString(36) + Date.now().toString(36)
                ).substr(2),
              })
            );
            setTimeout(() => {
              nativeForm.submit();
              this.loading = false;
            }, 1500);
          }
        }
        /*END */
      }
    },
  },
  computed: {
    ...mapState({
      snack: (store) => store.ecommerce.snack,
    }),
  },
};
</script>

